import snabbdom from '@servicenow/ui-renderer-snabbdom';
import { createCustomElement } from '@servicenow/ui-core';
import '@servicenow/now-text-link';
import '@servicenow/now-button';
import '@servicenow/now-label-value';
import '@servicenow/now-highlighted-value';
import style from './_now-record-compact.scss';
import {t} from 'sn-translate';

/** @seismicView now-record-compact */
const NowRecordCompact = (state, {dispatch}) => {
	const {properties} = state;
	const {actions} = properties;
	return (
		<div className="now-record-compact">
			{renderHeader(actions, dispatch)}
			{renderContent(properties, dispatch)}
		</div>
	);
};

const renderHeader = (actions, dispatch) => {
	return (
		<div className="now-record-compact--header">
			<now-button-iconic icon="chevron-left-outline" size="md" bare hide-padding variant="primary" config-aria={`{"aria-label": "${ t('Back') }"}`} on-click={(mouseEvent) => handleBackClick(dispatch, mouseEvent)}></now-button-iconic>
			{actions ? actions.map((action) => (renderAction(action, dispatch))) : null}
		</div>
	);
};

const renderAction = (action, dispatch) => {
	const {id, label} = action;
	return (
		<div className="now-record-compact--header--action">
			<now-text-link label={label} variant="secondary" on-click={(mouseEvent) => {handleAction(mouseEvent, dispatch, id);}}></now-text-link>
		</div>
	);
};

const handleBackClick = (dispatch) => {
	dispatch('NOW_RECORD_COMPACT#BACK_CLICKED');
};

const handleAction = (mouseEvent, dispatch, id) => {
	mouseEvent.preventDefault();
	dispatch('NOW_RECORD_COMPACT#ACTION_CLICKED', {actionId: id});
};

const handleLinkClick = (mouseEvent, dispatch) => {
	mouseEvent.preventDefault();
	dispatch('NOW_RECORD_COMPACT#ACTION_CLICKED', {actionId: 'link'});
};

const renderContent = (properties, dispatch) => {
	const {headerTagline, headerHighlightedValue, headerTitle, linkValue, bodyTitle, items, footerLabel, footerValue} = properties;
	return (
		<div className="now-record-compact--content">
			<div className="now-record-compact--content--header">
				{renderMetaData(headerTagline, headerHighlightedValue)}
				{renderHeaderTitle(headerTitle)}
				{renderLinkValue(linkValue, dispatch)}
			</div>
			<div className="now-record-compact--content--body">
				{renderBodyTitle(bodyTitle)}
				{renderFields(items)}
			</div>
			{renderFooter(footerLabel, footerValue)}
		</div>
	);
};

const renderHeaderTitle = (headerTitle) => {
	if (!headerTitle)
		return;

	return (
		<div className="now-record-compact--content--header--title text--secondary">{headerTitle}</div>
	);
};

const renderMetaData = (headerTagline, headerHighlightedValue) => {
	if (!(headerTagline || headerHighlightedValue))
		return;

	return (
		<div className="now-record-compact--content--header--meta">
			<span className="text--tertiary">{headerTagline}</span>
			{renderHighlightedValue(headerHighlightedValue)}
		</div>
	);
};

const renderHighlightedValue = (headerHighlightedValue) => {
	if (!headerHighlightedValue)
		return;

	const {label, status, showIcon} = headerHighlightedValue;
	return (
		<div className="now-record-compact--content--header--meta--badge">
			<now-highlighted-value label={label}
				status={status}
				showIcon={showIcon}>
			</now-highlighted-value>
		</div>
	);
};

const renderLinkValue = (linkValue, dispatch) => {
	if (!linkValue)
		return;

	return (
		<div className="now-record-compact--content--header--link">
			<now-text-link label={linkValue} variant="secondary" on-click={(mouseEvent) => {handleLinkClick(mouseEvent, dispatch);}}></now-text-link>
		</div>
	);
};
const renderBodyTitle = (bodyTitle) => {
	if (!bodyTitle)
		return;

	return (
		<div className="now-record-compact--content--body--title text--primary">{bodyTitle}</div>
	);
};

const renderFields = (items) => {
	if (!items)
		return;

	return (
		<now-label-value-stacked align="vertical" items={items} size="md" wrapped="false"></now-label-value-stacked>
	);
};

const renderFooter = (footerLabel, footerValue) => {
	if (!(footerLabel || footerValue))
		return;

	return (
		<div className="now-record-compact--content--footer text--tertiary">
			<div className="now-record-compact--content--footer--label">{footerLabel}</div>
			<div className="now-record-compact--content--footer--value">{footerValue}</div>
		</div>
	);
};

/**
 * @seismicComponent now-record-compact
 */
createCustomElement('now-record-compact', {
	properties: {
		actions: {},
		headerTagline: {},
		headerHighlightedValue:{},
		headerTitle: {},
		linkValue: {},
		bodyTitle: {},
		items:{},
		footerLabel: {},
		footerValue: {}
	},
	dispatches: {
		/**
		 * Dispatched when the user clicks on an action button, or selects an
		 * action. Payload will include a reference to the clicked action's id, if specified.
		 * @type {{action: Object}}
		 */
		'NOW_RECORD_COMPACT#ACTION_CLICKED': {},
		'NOW_RECORD_COMPACT#BACK_CLICKED':{}
	},
	renderer: {
		view: NowRecordCompact,
		type: snabbdom
	},
	styles: style
});
